import isArray from './isArray';
import isDate from './isDate';
import isRegExp from './isRegExp';
/**
 * @description 
 * Vuex https://github.com/vuejs/vuex/blob/dev/src/util.js 
 * , Date {time:new Date(), regexp:/\w+/}
 * @param {*} obj 
 * @param {Array<any>} [cache=[]] , cache
 * @return {*}  {*} 
 * @example deepCopy({age:12, time:new Date(), regexp:/\w+/, fn:function(){}}, [])
 */
const deepCopy = (obj, cache = []) => {
    if (obj === null || typeof obj !== 'object')
        return obj;
    let hit = cache.filter(function (c) {
        return c.original === obj;
    })[0];
    if (hit)
        return hit.copy;
    let copy;
    //DateRegExp
    if (isDate(obj))
        copy = new Date(obj);
    else if (isRegExp(obj))
        copy = new RegExp(obj);
    else if (isArray(obj))
        copy = [];
    else
        copy = {};
    cache.push({
        original: obj,
        copy,
    });
    Object.keys(obj).forEach((key) => {
        copy[key] = deepCopy(obj[key], cache);
    });
    return copy;
};
export default deepCopy;
