import isObj from './isObj';
/**
 * @description object
 *
 * @param {*} obj 
 * @return {*}  {Array<any>} 
 * @example toValues({a:1,b:2}) => [1, 2]
 */
const toValues = (obj) => {
    if (!isObj(obj))
        return [];
    let values = [];
    for (let key in obj) {
        values.push(obj[key]);
    }
    return values;
};
export default toValues;
