import toKeys from './toKeys';
import isObj from './isObj';
/**
 * @description objecturl
 *
 * @param {*} obj Url
 * @return {*}  {string}  ? Url
 * @example a=1&b=2&c=3
 */
const toParam = (obj) => {
    if (!isObj(obj))
        return '';
    const keys = toKeys(obj);
    let param = '';
    keys.forEach((key, i) => {
        if (i < keys.length - 1)
            param += `${key}=${obj[key]}&`;
        else
            param += `${key}=${obj[key]}`;
    });
    return param;
};
export default toParam;
