/**
 * @description  
 *
 * @class CheckRegExp
 */
class CheckRegExp {
    constructor() { }
    /**
     * @description 
     *
     * @static
     * @return {*}
     * @memberof CheckRegExp
     */
    static getInstance() {
        if (!this.$instance)
            this.$instance = new CheckRegExp();
        return this.$instance;
    }
    /**
     *
     * @description 
     *
     * @param {*} v 
     * @return {*}  {boolean} 
     * @memberof CheckRegExp
     * @example mobile(12345678) => return false
     */
    mobile(v) {
        let reg = /^1[3|4|5|6|7|8|9][0-9]{9}$/;
        return reg.test(v);
    }
    /**
     *@description 
     *
     * @param {*} v 
     * @return {*}  {boolean} 
     * @memberof CheckRegExp
     * @example telephone("1367894517") => false
     */
    telephone(v) {
        let reg = /^(0\d{2,3}-\d{7,8})(-\d{1,4})?$/;
        return reg.test(v);
    }
    /**
     * @description  6~18
     *
     * @param {*} v 
     * @return {*}  {boolean} 
     * @memberof CheckRegExp
     * @example password("abc!@") => false
     */
    password(v) {
        let reg = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/;
        return reg.test(v);
    }
    /**
     * @description QQ
     *
     * @param {*} v 
     * @return {*}  {boolean} 
     * @memberof CheckRegExp
     * @example qq("12345") => false
     */
    qq(v) {
        let reg = /^[1-9][0-9]{4,9}$/;
        return reg.test(v);
    }
    /**
     * @description 
     *
     * @param {*} v 
     * @return {*}  {boolean} 
     * @memberof CheckRegExp
     * @example IDcard("2313213213") => false
     */
    IDcard(v) {
        let reg = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/;
        return reg.test(v);
    }
    /**
     * @description 
     *
     * @param {*} v 
     * @return {*}  {boolean} 
     * @memberof CheckRegExp
     * @example postal("511740") => true
     */
    postal(v) {
        let reg = /\d{6}/;
        return reg.test(v);
    }
    /**
     * @description URL
     *
     * @param {*} v 
     * @return {*}  {boolean} 
     * @memberof CheckRegExp
     * @example url("http://127.0.0.1") => true url("http://www.abc.com") => true
     */
    url(v) {
        let reg = /(http|ftp|https):\/\/[\w\-_]+(\.[\w\-_]+)+([\w\-\.,@?^=%&:/~\+#]*[\w\-\@?^=%&/~\+#])?/;
        return reg.test(v);
    }
    /**
     * @description IP
     *
     * @param {*} v 
     * @return {*}  {boolean} 
     * @memberof CheckRegExp
     * @example IPv4("127.0.0.1") => true
     */
    IPv4(v) {
        let reg = /^(?:(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.){3}(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)$/;
        return reg.test(v);
    }
    /**
     * @description 
     *
     * @param {*} v 
     * @return {*}  {boolean} 
     * @memberof CheckRegExp
     * @example number("123") => true number("s1") => false
     */
    number(v) {
        let reg = /^[0-9]$/;
        return reg.test(v);
    }
    /**
     * @description 
     *
     * @param {*} v 
     * @return {*}  {boolean} 
     * @memberof CheckRegExp
     * @example en("cn") => true
     */
    en(v) {
        let reg = /^[a-zA-Z]+$/;
        return reg.test(v);
    }
    /**
     * @description 
     *
     * @param {*} v 
     * @return {*}  {boolean} 
     * @memberof CheckRegExp
     * @example cn("") => true  cn("cn") => false
     */
    cn(v) {
        let reg = /^[\u4E00-\u9FA5]+$/;
        return reg.test(v);
    }
    /**
     * @description HTML
     *
     * @param {*} v 
     * @return {*}  {boolean} 
     * @memberof CheckRegExp
     * @example html("<div></div>") => true
     */
    html(v) {
        let reg = /<("[^"]*"|'[^']*'|[^'">])*>/;
        return reg.test(v);
    }
    /**
     * @description 
     *
     * @param {*} v 
     * @return {*}  {boolean} 
     * @memberof CheckRegExp
     * @example email("1324568@qq.com") => true
     */
    email(v) {
        let reg = /^[\w-]+(\.[\w-]+)*@[\w-]+(\.[\w-]+)+$/;
        return reg.test(v);
    }
    /**
     * @description 
     *
     * @param {*} v 
     * @return {*}  {boolean} 
     * @memberof CheckRegExp
     * @example username(abc_132) => true
     */
    username(v) {
        let reg = /^[a-zA-Z][a-zA-Z0-9_]{4,15}$/;
        return reg.test(v);
    }
}
const check = CheckRegExp.getInstance();
export default check;
