import typeOf from "./typeOf";
/**
 * @description 
 * @param v any 
 * @return boolean boolean
 * @example isPrimitive(1) => true isPrimitive({}) => false
 */
const isPrimitive = (v) => {
    return (typeOf(v, "string") ||
        typeOf(v, "number") ||
        typeOf(v, "symbol") ||
        typeOf(v, "boolean"));
};
export default isPrimitive;
