import isObj from './isObj';
import indexOf from './indexOf';
/**
 * @description 
 *
 * @param {(any[] | string)} data 
 * @param {never} ele , ,,
 * @param {(number | undefined)} [fromIndex]  
 * @return {*}  {boolean} 
 * @example includes([1,2,3], 1) => true
 */
const includes = (data, ele, fromIndex) => {
    let index = fromIndex || 0;
    let i = Math.max(index >= 0 ? index : data.length - Math.abs(fromIndex), 0);
    while (i < data.length) {
        if (isObj(ele)) {
            return indexOf(data, ele, fromIndex) !== -1;
        }
        else {
            if ('' + data[i] === '' + ele) {
                return true;
            }
        }
        i++;
    }
    return false;
};
export default includes;
