/**
 * @description 
 *
 * @param {{ apply: (args0: any, args1: any) => void }} fn 
 * @param {number} [delay=500]  500ms
 * @return {*}  {Function} 
 */
const debounce = (fn, delay = 500) => {
    let timeout = null;
    return function (...args) {
        if (timeout)
            clearTimeout(timeout);
        timeout = setTimeout(() => {
            timeout = null;
            fn.apply(this, args);
        }, delay);
    };
};
export default debounce;
