/**
 * @description 
 *
 * @param {*} v 
 * @param {Types} t  'string'| 'number'| 'object'| 'function'| 'boolean'| 'undefined'| 'symbol'| 'bigint'
 * @return {*}  {boolean} 
 * @example typeOf(true, "boolean") = true
 */
const typeOf = (v, t) => {
    return typeof v === t;
};
export default typeOf;
