/**
 *@description WPhone
 *
 * @return {*}  {boolean} 
 * @example isWPhone() => false|true
 */
const isWPhone = () => {
    let userAgentInfo = navigator.userAgent;
    if (userAgentInfo.indexOf('Windows Phone') > -1) {
        return true;
    }
    return false;
};
export default isWPhone;
