/**
 * @description 
 *
 * @param {number} [lower=0] 
 * @param {number} [upper=1] 
 * @return {*}  {number} [lower, upper]
 * @example random(0, 4) => 3
 */
const random = (lower = 0, upper = 1) => {
    return Math.floor(Math.random() * (upper - lower)) + lower;
};
export default random;
