/**
 * @description 
 *
 * @param {{ apply: (args0: any, args1: any) => void }} fn 
 * @param {number} [delay=500]  500ms
 * @return {*}  {Function} 
 */
const throttle = (fn, delay = 500) => {
    let lastTime = 0;
    return function (...args) {
        let now = new Date().getTime();
        if (now - lastTime > delay) {
            fn.apply(this, args);
            lastTime = now;
        }
    };
};
export default throttle;
