import isObj from './isObj';
/**
 * @description object
 *
 * @param {*} obj 
 * @return {*}  {Array<string>} 
 * @example toKeys({a:1,b:2}) => ["a", "b"]
 */
const toKeys = (obj) => {
    if (!isObj(obj))
        return [];
    let keys = [];
    for (let key in obj) {
        keys.push(key);
    }
    return keys;
};
export default toKeys;
