import { hasOwnProperty } from './init';
import isPrimitive from './isPrimitive';
/**
 * @description 
 *
 * @param {*} obj , obejctfalse
 * @param {(string|number)} v 
 * @return {*}  {boolean} 
 * @example has({a:1}, "a") => true
 */
const has = (obj, v) => {
    if (isPrimitive(obj))
        return false;
    return hasOwnProperty.call(obj, v);
};
export default has;
